'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { supabase } from '@/lib/supabaseClient';

export default function CoursePage({ params }) {
  const router = useRouter();
  const [user, setUser] = useState(null);
  const [course, setCourse] = useState(null);
  const [pdfs, setPdfs] = useState([]);
  const [videos, setVideos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      const parsed = JSON.parse(storedUser);
      setUser(parsed);
    }
    fetchCourseData();
  }, [params.id]);

  const fetchCourseData = async () => {
    try {
      // Fetch course details
      const { data: courseData, error: courseError } = await supabase
        .from('cours')
        .select('*')
        .eq('id', params.id)
        .single();

      if (courseError) throw courseError;
      setCourse(courseData);

      // Fetch PDFs
      const { data: pdfsData, error: pdfsError } = await supabase
        .from('course_pdfs')
        .select('*')
        .eq('course_id', params.id)
        .order('created_at', { ascending: false });

      if (pdfsError) throw pdfsError;
      setPdfs(pdfsData);

      // Fetch videos
      const { data: videosData, error: videosError } = await supabase
        .from('course_videos')
        .select('*')
        .eq('course_id', params.id)
        .order('created_at', { ascending: false });

      if (videosError) throw videosError;
      setVideos(videosData);

    } catch (error) {
      console.error('Error fetching course data:', error);
      setError('Erreur lors du chargement des données du cours');
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-900 text-white flex justify-center items-center">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-500"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900 text-white">
      {/* NAVBAR */}
      <nav className="flex justify-between items-center bg-gray-800 text-white px-6 py-3 shadow-md">
        <div className="flex gap-6">
          <a href="/dashboard" className="hover:underline font-semibold">Accueil</a>
          <a href="/cours" className="hover:underline">Cours</a>
          <a href="/offres" className="hover:underline">Offres</a>
          <a href="/dossiers" className="hover:underline">Dossiers</a>
        </div>
        <div className="flex items-center gap-4">
          {user && <div className="italic">Bonjour, {user.name}</div>}
        </div>
      </nav>

      <div className="p-8">
        <h1 className="text-3xl font-bold mb-8 text-center">{course?.title}</h1>

        {error && (
          <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
            {error}
          </div>
        )}

        {/* PDFs Section */}
        <div className="mb-12">
          <h2 className="text-2xl font-semibold mb-6">PDFs du cours</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {pdfs.map((pdf) => (
              <div key={pdf.id} className="bg-[#121826] border border-white rounded-2xl p-6 shadow-md hover:shadow-xl transition">
                <h3 className="font-semibold text-lg mb-3">{pdf.title}</h3>
                <a
                  href={pdf.pdf_url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-blue-400 hover:underline block"
                >
                  Voir le PDF
                </a>
              </div>
            ))}
          </div>
        </div>

        {/* Videos Section */}
        <div>
          <h2 className="text-2xl font-semibold mb-6">Vidéos YouTube</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {videos.map((video) => (
              <div key={video.id} className="bg-[#121826] border border-white rounded-2xl p-6 shadow-md hover:shadow-xl transition">
                <h3 className="font-semibold text-lg mb-3">{video.title}</h3>
                <a
                  href={video.youtube_url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-blue-400 hover:underline block"
                >
                  Voir la vidéo
                </a>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
} 